import React from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom';

import Main from '../../pages/main';
import Pumping from '../../pages/pumping';
import Pumped from '../../pages/pumped';

import './app.css';

const App = () => {
    const { REACT_APP_DEPLOY_FOLDER } = process.env;
    const DEPLOY_FOLDER = REACT_APP_DEPLOY_FOLDER ? '/' + REACT_APP_DEPLOY_FOLDER : '';
    
    return (
        <div className="App" >
            <Router>
                <Route path={`${DEPLOY_FOLDER}/`} component={ Main } exact/>

                <Route path={`${DEPLOY_FOLDER}/pumping`} component={ Pumping }/>

                <Route path={`${DEPLOY_FOLDER}/pumped`} component={ Pumped }/>
            </Router>
        </div>
    )
}

export default App;